
.timeline-section {
	position: relative;
    margin: 30px 0;
}

.timeline-section::after {
	content: "";
    position:absolute;
	left: 0;    
	right: 0;
	height: 3px; 
	background-color: #E31277;
}

.timeline-pointer{
 position: relative;
  display: flex;
  justify-content: center;
}

.timeline-pointer::before {
	content: "";
    position:absolute;
	left: 50%;
	width: 8px;
	height: 8px;
	border-radius: 50%;
	transform: translate(-50%,-25%);
	background-color: #E31277;
}